

































































  import poster from '../../components/poster.vue'
  
	export default{
    components: {
      poster
    },
		data(){
			return{
				hotLists: [],
        shareData: {},
        shareTipsData: {},
        posterData: {},
        clickShareLinking: false
			}
		},
    computed: {
      userInfo() {
        return this.$store.state.userInfo
      },
      config() {
        return this.$store.state.config
      },
      constant() {
        return this.$constant
      },
      isRole() {
        return this.userInfo.role > 0
      }
    },
    onLoad() {
    	this.$http({
        url: this.$apis.articleList,
        method: 'GET',
        data: {
          hot_recommend: 1
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.hotLists = data.data.data
          }
        }
      })
    },
		methods:{
      gotoPoster() {
        if (!this.isRole) {
          uni.showModal({
          	title: '提示',
            content: '您是临时用户，无权使用此功能，请先免费升级为普通会员继续使用。',
            cancelText: '知道了',
            confirmText: '去升级',
            success: res => {
            	if (res.confirm) {
                this.$navigateTo({
                	url: '/pages/mine/agent'
                })
              }
            }
          })
          return
        }
        this.$navigateTo({
        	url: '/pages/spread/poster'
        })
      },
      gotoMaterial() {
        this.$navigateTo({
        	url: '/pages/spread/material'
        })
      },
      gotoExp() {
        this.$navigateTo({
        	url: '/pages/spread/experience'
        })
      },
      gotoHot() {
        this.$navigateTo({
        	url: '/pages/spread/hot'
        })
      },
      clickShareLink() {
        if (this.clickShareLinking) {
          uni.showToast({
          	title: '分享中，请勿频繁操作',
            icon: 'none'
          })
          return
        }
        this.clickShareLinking = true
        let str = this.$constant.shareUrl + '?pid=' + this.userInfo.id;
        uni.getProvider({
        	service: 'share',
          success: res => {
            if (~res.provider.indexOf('weixin')) {
              uni.share({
              	provider: 'weixin',
              	type: 0,
                title: this.$store.state.config.system_nick_name,
                scene: 'WXSceneSession',
                summary: this.$store.state.config.platform_info,
                href: str,
                imageUrl: this.$store.state.config.logo,
              	success: res => {
                  this.clickShareLinking = false
                },
              	fail: err => {
                  this.copy(str)
                },
              	complete: () => {
                  this.clickShareLinking = false
                }
              });
            } else {
              this.clickShareLinking = false
              this.copy(str)
            }
          },
          fail: err => {
            this.clickShareLinking = false
          	this.copy(str)
          }
        })
      },
      clickSharePoster() {
        this.posterData = {
          show: true,
          code: 'platform',
          url: this.$constant.shareUrl + '?pid=' + this.userInfo.id
        }
      },
      copy(str) {
        uni.setClipboardData({
        	data: str,
          success: () => {
            uni.showModal({
              title: '复制成功',
              content: '链接已复制，请手动粘贴发送给好友',
              showCancel: false
            })
          },
          fail: () => {
            uni.showModal({
              title: '复制失败',
              content: str,
              showCancel: false
            })
          }
        })
      },
      gotoContent(id) {
        this.$navigateTo({
        	url: '/pages/spread/content?aId=' + id
        })
      }
		}
	}
